<template>
  <div>
    <h2>HOME组件内容</h2>
    <ul class="nav nav-tabs">
      <li>
        <router-link
          class="list-group-item"
          active-class="active"
          to="/home/news"
          >News</router-link
        >
      </li>
      <li>
        <router-link
          class="list-group-item"
          active-class="active"
          to="/home/msg">
          Message
        </router-link>
      </li>
    </ul>
    <!-- include表示想要缓存的组件名 activated和deactivated没有keep-alive的时候是不会被触发的-->
    <keep-alive :include='["news-lk","msg-lk"]'>
    <router-view></router-view>
    </keep-alive>
  </div>
</template>

<script>
export default {
  name: 'home-lk',
  // 只有通过route进入才会执行方法，其他方法进入不算
  beforeRouteEnter(to, from, next){
        console.log('组件'+to.name+'内守卫启动了' + '而且来自' + from.name + '去往' + to.name);
        next();
  },
  beforeRouteLeave(to, from, next){
        console.log('组件'+from.name+'内守卫启动了' + '而且来自' + from.name + '去往' + to.name);
        next();
  }
}
</script>

