//用于创建路由器
import vueRouter from 'vue-router'
import about from '../pages/about.vue'
import home from '../pages/home.vue'
import news from '../pages/news.vue'
import msg from '../pages/msg.vue'
import detail from '../pages/deTail.vue'

const router = new vueRouter({
  routes: [
    {
      name: 'home',
      path: '/home',
      component: home,
      meta:{
        title:'主页'
      },
      children: [
        {
          name: 'news',
          path: 'news',
          component: news,
          meta:{
            title:'新闻'
          },
        },
        {
          name: 'msg',
          path: 'msg',
          component: msg,
          meta:{
            title:'信息'
          },
          children: [
            {
            name: 'detailname',
            // path: 'detail',
            path: 'detail/:id/:title',
            //当为true时，则把路由收到的所有params参数通过props传给Detail（当前所在）组件
            props: true,
            component: detail,
            meta:{
              title:'详细信息'
            },
          }]
        }

      ]
    },
    {
      name: 'about',
      path: '/about',
      component: about,
      meta:{
        title:'关于'
      },
      beforeEnter(to,from,next){
        console.log('进入about,独享守卫启动了'+'而且来自' + from.name + '去往' + to.name);
        next();
      }
    }
  ],
})
// 每次路由切换之前执行
router.beforeEach(
  (to, from, next) => {
    console.log('来自' + from.name + '去往' + to.name);
    if (to.name == 'msg') {
      alert(`无权限访问${to.name}`);
      return;
    }
    next();
  }
);
router.afterEach( to => document.title = to.meta.title || 'lk-demo')
export default router